import React from "react";
import { ZsearchForm } from "zerod";
import { Input, message, DatePicker } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/ZsearchForm/doc.md";
export default AmdDocHOC(doc, {
	demo1: function() {
		class Myjavascript extends React.PureComponent {
			items = [
				{
					key: "serviceCode",
					label: "服务编码",
					render: (form) => {
						//异步加载表单控件
						return new Promise((resolve) => {
							setTimeout(() => {
								resolve(<Input placeholder="请输入服务编码" />);
							}, 5000);
						});
					},
					//antd的 form.getFieldDecorator的options
					options: {
						//验证规则
						rules: [
							{
								required: true,
								message: "不能为空。",
							},
						],
					},
				},
				{
					key: "serviceName",
					label: "服务名称",
					render: (form) => {
						return <Input placeholder="请输入服务名称" />;
					},
					//antd的 form.getFieldDecorator的options
					options: {
						//验证规则
						rules: [
							{
								required: true,
								message: "不能为空。",
							},
						],
					},
				},
				{
					key: "time",
					label: "申请时间",
					render: (form) => {
						return <DatePicker.RangePicker renderExtraFooter={() => "extra footer"} showTime />;
					},
				},
				{
					key: "serviceRemark",
					label: "服务说明",
					span: 24,
					render: (form) => {
						return <Input.TextArea rows={2} placeholder="请输入服务说明" />;
					},
					//antd的 form.getFieldDecorator的options
					options: {
						//验证规则
						rules: [
							{
								required: true,
								message: "不能为空。",
							},
						],
					},
				},
			];
			render() {
				return (
					<ZsearchForm
						labelLayout="inline"
						colFormItems={this.items}
						onSearch={(values) => {
							message.success("提交成功：" + JSON.stringify(values));
						}}
						collapseCount={2}
					/>
				);
			}
		}
		return <Myjavascript />;
	},
});
